ecute(async () => { await authentication.signOut(); }, { loading: true });\n}\n\nfunction openFeedbackDialog () {\n  dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/dialogs/giveUsFeedbackDialog.vue`)),\n        { closable: true, title: \"Give Us Feedback\", width: \"540px\", maxHeight: \"706px\" },\n    );\n}\nfunction goToProfile() {\n  router.push(\"/completeProfile\");\n}\nfunction goToUrl(url: string) {\n  window.open(url, \"_blank\")\n}\n</script>\n<template>\n  <div class=\"page-content\">\n    <nav>\n      <div class=\"container-fluid\">\n        <div class=\"row row-responsive header\">\n          <div class=\"col-xl-2\">\n            <img v-if=\"viewModel.user?.selectedOrganization?.accountType === 'premium'\" src=\"../../../../images/plateau_logo-navy.svg\" alt=\"\" width=\"205\" height=\"36\" class=\"logo\" />\n            <img v-else src=\"../../../../images/plateau_logo-beta.png\" alt=\"\" width=\"205\" height=\"36\" />\n          </div>\n\n          <div class=\"col-xl-8\">\n            <a class=\"documantationsText\" href=\"https://studio.onplateau.com/docs/ui/docs/Introduction/studio-description\" target=\"_blank\">Documentation</a>\n            <a style=\"padding-left: 34px\" href=\"https://www.youtube.com/@PlateauStudio\" target=\"_blank\" class=\"documantationsText\">Academy</a>\n            <a style=\"padding-left: 34px\" href=\"https://discourse.softtech.com.tr/\" target=\"_blank\" class=\"documantationsText\">Discourse</a>\n          </div>\n\n          <div class=\"navbar-icons col-xl-2\">\n            <div class=\"col-xl-1 p-0 dot-icon\" style=\"display: flex\">\n              <img src=\"../../../../images/profile-image-dark-header.svg\" class=\"profile-icon\" style=\"margin-right: 4px;\"  id=\"dropdownMenuButton1\" data-bs-toggle=\"dropdown\" aria-expanded=\"false\" data-bs-target=\"#dropdown-menu\"/>\n              <i\n                class=\"mdi mdi-chevron-down chevronIcon\"\n                data-bs-toggle=\"dropdown\"\n                aria-expanded=\"false\"\n                data-bs-target=\"#dropdown-menu\"\n                id=\"dropdownMenuButton1\"\n                style=\"padding-top: 10px; margin-right: -11px; font-size: 16px\"\n              ></i>\n              <ul style=\"width: 246px\" class=\"dropdown-menu user-info-dropwdown\" aria-labelledby=\"dropdownMenuButton1\">\n                <li>\n                  <div class=\"user-info\">\n                    <img src=\"../../../../images/profile-image-dark-in-menu.svg\" style=\"margin-right: 12px;\"/>\n                    <div style=\"display: grid;\">\n                      <span class=\"user-name\">{{ user.name + ' ' + user.lastName }}</span>\n                      <span class=\"user-email\">{{ user.email }}</span>\n                    </div>\n                    \n                  </div>\n                </li>\n                <li>\n                  <div class=\"menuıtem\" @click=\"openFeedbackDialog()\">\n                    <i class=\"mdi mdi-lifebuoy\" style=\"font-size: 20px; margin-right: 8px;\"></i>\n                    <a>Give Us Feedback</a>\n                  </div>\n                </li>\n                <li>\n                  <div class=\"menuıtem\" @click=\"goToUrl('https://studio.onplateau.com/docs/ui/docs/Introduction/studio-description')\">\n                    <i class=\"mdi mdi-file-document-multiple-outline\" style=\"font-size: 20px; margin-right: 8px;\"></i>\n                    <a>Documentation</a>\n                  </div>\n                </li>\n                <li>\n                  <div class=\"menuıtem\" @click=\"goToUrl('https://www.youtube.com/@PlateauStudio')\">\n                    <i class=\"mdi mdi-school-outline\" style=\"font-size: 20px; margin-right: 8px;\"></i>\n                    <a>Academy</a>\n                  </div>\n                </li>\n                <li>\n                  <div class=\"menuıtem\" @click=\"goToUrl('https://discourse.softtech.com.tr/')\">\n                    <i class=\"mdi mdi-comment-multiple-outline\" style=\"font-size: 20px; margin-right: 8px;\"></i>\n                    <a>Discourse</a>\n                  </div>\n                </li>\n                <li v-if=\"isBackOfficeAvailable\">\n                  <div class=\"menuıtem\" @click=\"gotoBackOffice\">\n                    <i class=\"mdi mdi-chart-line\" style=\"font-size: 20px; margin-right: 8px;\"></i>\n                    <a>Back Office</a>\n                  </div>\n                </li>\n                <li>\n                  <div class=\"menuıtem\" @click=\"logout()\">\n                    <i class=\"mdi mdi-logout\" style=\"font-size: 20px; margin-right: 8px;\"></i>\n                    <a>Logout</a>\n                  </div>\n                </li>\n              </ul>\n            </div>\n          </div>\n          <!-- <div class=\"py-1 navbar-icons\">\n                        <div class=\"media d-flex align-items-center\">\n                            <i class=\"mdi mdi-bell bell\"></i>\n                        </div>\n                    </div>\n\n                    <div class=\"py-1 navbar-icons\">\n                        <div class=\"media d-flex align-items-center\">\n                            <i class=\"mdi mdi-forum forum\"></i>\n                        </div>\n                    </div> -->\n        </div>\n      </div>\n    </nav>\n  </div>\n</template>\n<style scoped>\n@import url(\"../../../../styles/IDE3.css\");\n.user-info-dropwdown {\n  width: 246px;\n  border: 1px solid #E3E3E3;\n  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n  border-radius: 8px;\n}\n.menuıtem:hover {\n  background-color: #d5d5d5 !important;\n  cursor: pointer;\n}\n.menuıtem {\n  font-size: 14px;\n  line-height: 16px;\n  color: #212121;\n  display: flex;\n  align-items: center;\n  padding: 8px 12px;\n}\n.user-info {\n  display: flex;\n  align-items: center;\n  padding: 10px 12px 20px;\n}\n.user-name {\n  font-weight: 500;\n  font-size: 14px;\n  line-height: 16px;\n  color: #212121;\n  margin-bottom: 8px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n.user-email {\n  font-size: 14px;\n  line-height: 16px;\n  color: #757575;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n.chevronIcon {\n  cursor: pointer;\n}\n.documantationsText {\n  margin-top: 28px;\n  color: #212121;\n  font-family: \"Roboto\";\n  cursor: pointer;\n  text-decoration: none;\n  font-size: 17px;\n  font-weight: 500;\n}\n.out-link-text {\n  color: #212121;\n  text-decoration: none;\n}\n@media (max-width: 1350px) {\n  .row-responsive > * {\n    width: unset;\n  }\n}\n\n.header {\n  min-height: 72px;\n  background: #ECEFF1 0% 0% no-repeat padding-box;\n  border-bottom: 1px solid #E3E3E3;\n  position: absolute;\n  width: 100%;\n  align-items: center;\n}\n</style>\n"],"sourceRoot":""}]);
